@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap");

$white: hsl(0, 0%, 100%);
$light-gray: hsl(212, 45%, 89%);
$grayish-blue: hsl(220, 15%, 55%);
$dark-blue: hsl(218, 44%, 22%);

body {
  font-family: "Outfit", sans-serif;
  background-color: $light-gray;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#card {
  background-color: $white;
  height: 31.25rem;
  width: 20rem;
  margin-top: 9.375rem;
  border-radius: 1.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

#qr-code-container {
  padding-top: 0.938rem;
  padding-left: 0.938rem;
  padding-right: 0.938rem;

  img {
    max-width: 100%;
    border-radius: 0.625rem;
  }
}

#caption {
  text-align: center;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  max-width: 80%;

  h1 {
    margin: 0;
    font-size: 1.375rem;
    color: $dark-blue;
  }

  p {
    margin: 0;
    margin-top: -0.75rem;
    color: $grayish-blue;
  }
}

.attribution {
  font-size: 0.688rem;
  text-align: center;
  margin-top: 1.25rem;

  a {
    color: hsl(228, 45%, 44%);
  }
}
